.Block {
  background-color: #545454;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.wrapper {
  width: 80%;
  padding: 100px 0;
  display: flex;
  border-radius: 5%;
  justify-content: space-between;
  flex-direction: column;
}
.skills {
  display: flex;
  align-items: center;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  line-height: 1.2;
  letter-spacing: 5px;
  font-size: 36px;
  text-transform: capitalize;
  color: #fffefe;
}
.myWorks {
  width: 100%;
  height: 10%;
  justify-content: center;
  align-items: center;
  border-radius: 2px;
  position: relative;
  z-index: 1;
}
.line {
  background-color: rgba(127, 255, 0, 0.73);
  width: 55px;
  height: 1px;
  margin: 30px auto;
}

.projects {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
}
.project {
  display: flex;
  flex-direction: column;
  background: #a7a7a7;
  margin: 10px 24px;
  justify-content: center;
  width: 41%;
  box-shadow: 8px 8px 4px antiquewhite;
  position: relative;
}
.project:hover {
  background: wheat;
  transition: 500ms;
}
.projectImg {
  height: 300px;
  background-color: black;
  background-image: url("./image/blog1.jpg");
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.projectImg:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 300px;
  background: black;
  opacity: 0.7;
}
.projectImg:hover::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 300px;
  opacity: 0;
}
.projectTitle {
  margin: 15px 0 15px;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: 1px;
  text-align: center;
  box-shadow: #545454;
}

.description {
  margin: 15px;
  text-align: center;
}
.btnShow {
  width: 150px;
  height: 50px;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 700;
  border-radius: 70%;
}

.content {
  background-color: #000000;
  color: wheat;
}

@media screen and (max-width: 650px) {
  .projects {
    flex-direction: column;
    flex-wrap: nowrap;
  }
  .project {
    width: 100%;
  }
}
